
$spacer: 15px;
$color1: red; 
$color2: green; 


$is_debug: false;


.typed {
  color: #c00;
}

.typed-cursor {
  color: green;
}

body.home h1{
    font-size: 3em;
}

.grid-item {
  width: 100%; 
  overflow: hidden;
  @include debug(3px, green);

  a {
    overflow: hidden;
    position: relative;
    margin: $spacer;
    display: block;
    @include debug(3px, red);

    .info {
      position: absolute;
      bottom: 30px;
      //background-color: rgba(0,0,0,.7);
      color: white;
      width: 100%;
      padding: $spacer;
      transition: 0.25s all 0.25s;
      opacity: 0;

      .title {
        font-size: 2em;
      }

      ul.categories {
        padding: 0;
        margin: 0;
        li {
          display: inline-block;
          list-style-type: none;
          background-color: rgba($color: $color2, $alpha: 0.8);
          padding: $spacer/6;
          padding-left: $spacer/3;
          padding-right: $spacer/3;
          margin-right: $spacer/3;
        }
      }
    }

    &:hover {
      .info {
        opacity: 1;
        bottom: 0;
      }
    }

    &:after{
      content: " ";
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: $color2;
      top: 100%;
      left: 0;
      z-index:2;
      transition:all 2s;
    }
    
  }

  &.visible a:after {
    top:-110%;
  }

}



